<template>
    <div class="content">
        <div class="leftDiv">
            <h2 class="title">Where do you go ?</h2>
            <div id="floating-panel">
                <b>Transport: </b>
                <select id="mode" v-model="vehicule">
                    <option value="DRIVING">Car</option>
                    <option value="WALKING">Walk</option>
                    <option value="BICYCLING">Bike</option>
                </select>
            </div>
            <p>
                <b>From: </b>
                <select id="directionFrom" v-model="setStart">
                    <option v-if="setEnd !== homeLocation" :value="homeLocation">Home</option>
                    <option v-if="setEnd !== workLocation" :value="workLocation">Work</option>
                    <option v-if="setEnd !== sportLocation" :value="sportLocation">Sport</option>
                    <option v-if="setEnd !== epitechLocation" :value="epitechLocation">Epitech</option>
                </select>
            </p>
            <p>
            <b>To: </b>
                <select id="directionTo" v-model="setEnd">
                    <option v-if="setStart !== homeLocation" :value="homeLocation">Home</option>
                    <option v-if="setStart !== workLocation" :value="workLocation">Work</option>
                    <option v-if="setStart !== sportLocation" :value="sportLocation">Sport</option>
                    <option v-if="setStart !== epitechLocation" :value="epitechLocation">Epitech</option>
                </select>
                <!--<label>-->
                    <!--<gmap-autocomplete @place_changed="setStartLocation"></gmap-autocomplete>-->
                <!--</label>-->
                <!--<label>-->
                    <!--<gmap-autocomplete @place_changed="setEndLocation"></gmap-autocomplete>-->
                <!--</label>-->
            </p>
            <button @click="getRoute">Itinerary</button>
            <br/>
            <div>
                <div>Distance: {{distance}}</div>
                <div>Time: {{duration}}</div>
            </div>
        </div>
        <div class="maps">
            <div>
                <gmap-map ref="mymap"
                          :center="center"
                          :zoom="12"
                          style="width:100%;  height: 400px;">
                    <gmap-marker
                            :key="index"
                            v-for="(m, index) in markers"
                            :position="m.position"
                            @click="center=m.position"
                    ></gmap-marker>
                </gmap-map>
            </div>
        </div>
    </div>
</template>

<script>
  // import Vue from 'vue'
  /* global google */
  import * as VueGoogleMaps from "vue2-google-maps";

  window.Vue.use(VueGoogleMaps, {
    load: {
      key: "AIzaSyBn-mcSKcjCoAwqYOsTBRWHIAq8QBj784E",
      libraries: "places, directions" // necessary for places input
    }
  });

  export default {
    name: "GoogleMap",
    data() {
      return {
        center: {lat: 48.8534, lng: 2.3488},
        markers: [],
        places: [],
        currentPlace: null,
        vehicule: "DRIVING",
        distance: "",
        duration: "",
        setStart: "",
        setEnd: "",
        homeLocation: "16 cours balguerie stuttenberg, Bordeaux, France",
        workLocation: "7 cours cabirol, Bordeaux, France",
        sportLocation: "Millesime crossfit, Bordeaux, France",
        epitechLocation: "Epitech Paris Parmentier"
      };
    },

    mounted() {
      this.$gmapApiPromiseLazy().then(() => {
        this.directionsService = new google.maps.DirectionsService()
        this.directionsDisplay = new google.maps.DirectionsRenderer()
        this.directionsDisplay.setMap(this.$refs.mymap.$mapObject)
        this.distanceMatrix = new google.maps.DistanceMatrixService()
      })

    },

    methods: {

      // receives a place object via the autocomplete component
      getRoute: function () {
        this.directionsService.route({
          origin: this.setStart,
          destination: this.setEnd,
          travelMode: google.maps.TravelMode[this.vehicule]
        }, (response, status) => {
          if (status === 'OK') {
              this.directionsDisplay.setDirections(response)
          }
        }),
        this.distanceMatrix.getDistanceMatrix({
          origins: [this.setStart],
          destinations: [this.setEnd],
          travelMode: google.maps.TravelMode[this.vehicule],
          unitSystem: google.maps.UnitSystem.METRIC,
          avoidHighways: false,
          avoidTolls: false
        }, (response, status) => {
          if (status !== 'OK') {
            alert('Error was: ' + status);
          } else {
            if (response.rows.length > 0
              && response.rows[0].elements[0].duration
              && response.rows[0].elements[0].distance) {
              this.distance = response.rows[0].elements[0].distance.text
              this.duration = response.rows[0].elements[0].duration.text
            }
          }
        });
      }
    }
  };
</script>

